p {
  margin-bottom: 0px;
}
.miner {
  position: relative;
}
.miner .bg {
  width: 100%;
  position: absolute;
  z-index: -10;
  height: 0px;
}
.miner .bg .bg1 {
  position: relative;
}
.miner .bg .bg1 img {
  width: 100%;
  height: 200px;
}
.miner .bg .bg2 {
  position: relative;
  z-index: -11;
}
.miner .bg .bg2 img {
  width: 100%;
  z-index: -11;
  height: 600px;
}
.miner .bg .bg3 {
  position: relative;
  z-index: -11;
}
.miner .bg .bg3 img {
  width: 100%;
  height: 150px;
}
.miner .content {
  margin: 0 15px;
}
.miner .content .logobox img {
  margin-top: 15px;
  height: 60px;
}
.miner .content .headerbox {
  margin: 15px 0;
  text-align: center;
}
.miner .content .headerbox img {
  height: 120px;
}
.miner .content .userbox {
  background-color: rgba(0, 18, 89, 0.8);
  border-radius: 15px;
}
.miner .content .userbox .usercontent {
  padding: 10px;
  display: flex;
}
.miner .content .userbox .usercontent .users {
  flex: 1;
  border-right: 2px solid #ffffff;
  margin-right: 15px;
}
.miner .content .userbox .usercontent .users .img img {
  height: 50px;
}
.miner .content .userbox .usercontent .users div {
  text-align: center;
}
.miner .content .userbox .usercontent .users div p {
  color: #ffffff;
}
.miner .content .userbox .usercontent .userinfo {
  flex: 2;
}
.miner .content .userbox .usercontent .userinfo .username p {
  color: #ffffff;
  font-size: 12px;
}
.miner .content .userbox .usercontent .userinfo .usermoney {
  margin-top: 20px;
}
.miner .content .userbox .usercontent .userinfo .usermoney p {
  font-size: 16px;
  font-weight: bold;
  color: #ffffff;
}
.miner .content .userbox .usercontent .userbtn {
  flex: 1;
  line-height: 60px;
  text-align: center;
}
.miner .content .userbox .usercontent .userbtn .ant-btn {
  background-color: #0c9e84;
}
.miner .content .userbox .usercontent .userbtn .ant-btn:active {
  color: black;
}
.miner .content .listbox {
  border-radius: 15px;
  margin: 30px 0;
}
.miner .content .listbox .listcontent .top {
  border-top-left-radius: 15px;
  border-top-right-radius: 15px;
  background-color: #0c9e84;
  display: flex;
  padding-top: 10px;
  padding-left: 10px;
  padding-right: 10px;
  padding-bottom: 10px;
}
.miner .content .listbox .listcontent .top .left {
  flex: 2;
  display: flex;
}
.miner .content .listbox .listcontent .top .left .level p {
  color: #ffffff;
  line-height: 40px;
}
.miner .content .listbox .listcontent .top .left .levelimg img {
  height: 20px;
  line-height: 40px;
  padding: 0px 5px;
  margin-top: 10px;
}
.miner .content .listbox .listcontent .top .left .leveltitle p {
  color: #ff9600;
  line-height: 40px;
}
.miner .content .listbox .listcontent .top .right {
  flex: 3;
  display: flex;
}
.miner .content .listbox .listcontent .top .right .left {
  flex: 2;
  flex-wrap: wrap;
}
.miner .content .listbox .listcontent .top .right .left div {
  width: 100%;
}
.miner .content .listbox .listcontent .top .right .left div p {
  font-size: 12px;
  color: #ffffff;
}
.miner .content .listbox .listcontent .top .right .right {
  flex: 1;
  text-align: center;
  padding: 10px 0px 10px 5px;
}
.miner .content .listbox .listcontent .top .right .right .ant-btn {
  background-color: #ff9600;
  color: #ffffff;
}
.miner .content .listbox .listcontent .top .right .right .ant-btn:active {
  color: #ffffff;
}
.miner .content .listbox .listcontent .bottom {
  background-color: rgba(0, 18, 89, 0.8);
  border-bottom-left-radius: 15px;
  border-bottom-right-radius: 15px;
  padding-top: 10px;
  padding-left: 10px;
  padding-right: 10px;
}
.miner .content .listbox .listcontent .bottom .rowbox {
  color: #ffffff;
  display: flex;
}
.miner .content .listbox .listcontent .bottom .rowbox .leftbox {
  flex: 3;
  display: flex;
}
.miner .content .listbox .listcontent .bottom .rowbox .leftbox .left {
  flex: 2;
  margin-bottom: 10px;
}
.miner .content .listbox .listcontent .bottom .rowbox .leftbox .right {
  flex: 1;
}
.miner .content .listbox .listcontent .bottom .rowbox .rightbox {
  flex: 2;
  display: flex;
}
.miner .content .listbox .listcontent .bottom .rowbox .rightbox .left {
  flex: 2;
  text-align: right;
}
.miner .content .listbox .listcontent .bottom .rowbox .rightbox .right {
  flex: 1;
}
.miner .content .listbox .listcontent .bottom .rowbox .rightbox .right .ant-btn {
  background-color: #0c9e84;
}
.miner .content .listbox .listcontent .bottom .rowbox .rightbox .right .ant-btn:active {
  color: black;
}
.miner .content .contractbox {
  background-color: rgba(0, 18, 89, 0.8);
  border-radius: 15px;
}
.miner .content .contractbox .contractcontent {
  padding: 10px 10px 0px 10px;
}
.miner .content .contractbox .contractcontent p {
  padding-bottom: 10px;
  color: #ffffff;
  white-space: normal;
  word-break: break-all;
}
.miner .content .myinformation {
  background-color: rgba(0, 18, 89, 0.8);
  border-radius: 15px;
  margin: 30px 0;
}
.miner .content .myinformation .myinformationcontent {
  padding: 10px;
}
.miner .content .myinformation .myinformationcontent .head {
  display: flex;
  border-bottom: 1px solid #9fa0ad;
}
.miner .content .myinformation .myinformationcontent .head img {
  flex: 0 0 22px;
  height: 25px;
}
.miner .content .myinformation .myinformationcontent .head div {
  flex: 1;
}
.miner .content .myinformation .myinformationcontent .head div p {
  position: relative;
  bottom: -5px;
  margin-left: 10px;
}
.miner .content .myinformation .myinformationcontent div {
  margin-bottom: 10px;
}
.miner .content .myinformation .myinformationcontent div p {
  color: #ffffff;
}
.ant-btn {
  height: 20px !important;
  line-height: 9px !important;
}
.withdrawbox {
  width: 80% !important;
  border-radius: 15px;
}
.withdrawbox .ant-modal-title {
  font-weight: bold;
  text-align: center;
}
.withdrawbox .ant-modal-header {
  border-bottom: none !important;
}
.withdrawbox .ant-select {
  width: 100%;
}
.withdrawbox .ant-modal-body {
  padding: 0 24px 10px 24px;
}
.withdrawbox .withdrawboxcontent .contentitem {
  display: flex;
  padding: 10px 0;
}
.withdrawbox .withdrawboxcontent .contentitem .left {
  flex: 1;
}
.withdrawbox .withdrawboxcontent .contentitem .right {
  flex: 2;
}
.withdrawbox .withdrawboxcontent .contentitem .right p {
  margin-left: 20px;
  color: #888888;
  line-height: 32px;
  font-size: 12px;
}
.withdrawbox .withdrawboxcontent .footer {
  width: 100%;
  padding: 10px 0;
}
.withdrawbox .withdrawboxcontent .footer .footerbtn {
  text-align: center;
}
.withdrawbox .withdrawboxcontent .footer .footerbtn img {
  height: 35px;
}
.upgradebox {
  width: 80% !important;
  border-radius: 10px;
}
.upgradebox .ant-modal-content {
  background-color: black !important;
  border-radius: 15px;
}
.upgradebox .ant-modal-close-x {
  color: #ffffff;
}
.upgradebox .upgradeboxcontent .head .img {
  text-align: center;
}
.upgradebox .upgradeboxcontent .head .img img {
  height: 40px;
}
.upgradebox .upgradeboxcontent .listbox .listitem {
  display: flex;
  border: 1px solid #9fa0ad;
  padding: 8px 5px;
  margin-bottom: 10px;
  border-radius: 3px;
  color: #ffffff;
  flex-wrap: wrap;
}
.upgradebox .upgradeboxcontent .listbox .listitem .box {
  width: 100%;
  display: flex;
}
.upgradebox .upgradeboxcontent .listbox .listitem .detailinfo {
  flex: 3;
}
.upgradebox .upgradeboxcontent .listbox .listitem .detailinfo p {
  color: #ffffff;
  font-size: 12px;
}
.upgradebox .upgradeboxcontent .listbox .listitem .levelinfo {
  flex: 2;
  display: flex;
}
.upgradebox .upgradeboxcontent .listbox .listitem .levelinfo img {
  width: 21px;
  margin-right: 10px;
}
.upgradebox .upgradeboxcontent .listbox .listitem .levelinfo p {
  color: #ff9600;
}
.upgradebox .upgradeboxcontent .listbox .listboxitemin {
  border: 1px solid #c69459;
}
.upgradebox .upgradeboxcontent .footer img {
  text-align: center;
  width: 100%;
}
.userboxs {
  width: 80% !important;
  border-radius: 10px;
}
.userboxs .ant-modal-title {
  font-weight: bold;
  text-align: center;
}
.userboxs .ant-modal-body {
  padding: 0 24px !important;
  padding-bottom: 24px !important;
}
.userboxs .ant-modal-header {
  border-bottom: none !important;
}
.userboxs .usercontent {
  width: 100%;
  min-height: 80px;
  max-height: 80px;
  overflow-y: scroll;
  list-style: none;
}
.userboxs .usercontent li {
  padding: 5px 0;
}
.ant-btn:hover,
.ant-btn:focus {
  color: black;
  border: none;
}
.ant-btn,
.ant-btn {
  color: black;
  border: none;
}
.lookmodal {
  display: flex;
  position: absolute;
  z-index: 99;
}
.lookmodal div {
  text-align: center;
  flex: 1;
}
.lookmodal div .ant-statistic-title {
  color: #1c2635 !important;
}
.loading {
  position: fixed;
  width: 90px;
  height: 90px;
  top: 50%;
  left: 50%;
  margin-top: -45px;
  margin-left: -45px;
  color: #ffffff;
  z-index: 999;
  text-align: center;
  justify-content: space-around;
  align-items: center;
}
.loading .ant-spin-container {
  border-radius: 5px;
}
.loading .ant-alert-info {
  background-color: rgba(59, 59, 59, 0.9);
  border: none;
  color: #ffffff;
}
.loading .ant-alert-with-description .ant-alert-message {
  color: #ffffff;
}
.loading .ant-spin {
  color: #ffffff;
  font-size: 16px;
  height: 90px;
  background-color: rgba(59, 59, 59, 0.9);
  border-radius: 5px;
}
.loading .ant-spin-container ant-spin-blur {
  height: 90px;
}
.loading .ant-spin-nested-loading {
  height: 90px;
}
.loadingbox {
  display: none;
}
.ant-input-number {
  width: 100% !important;
}
.userlistbox {
  min-height: 90px;
  max-height: 180px;
  overflow-y: scroll;
}
.userlistbox .userlistboxbtn {
  background-color: #0c9e84;
}
